@import url('https://fonts.googleapis.com/css2?family=Salsa&display=swap');
*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    font-family: 'Salsa', cursive;
}
.background{
    width: 100%;
    padding: 0px 30px;
}
.img_home{
    border-radius: 5px;
    width: 100%;
    height: 600px;
    background: url('../img/Home_back.png');
    background-size: 100% 100vh;
    background-repeat: no-repeat;
    margin: 0px auto;
    display: block;
}
.Image_Cards img{
    width:100%;
    margin: 20px 10px;
}
.middle h2{
    font-family: 'Salsa';
    font-style: normal;
    font-weight: 400;
    font-size: 50px;
    line-height: 86px;
    text-align: left;
    color: #781107;
   width: fit-contents;
   margin: 21px 0px;
}
.padding_left{
    padding-left: 100px;
}
.img_home p{
    margin-top: 20px;
    font-family: 'Salsa';
    font-style: normal;
    font-weight: 400;
    font-size: 35px;
    line-height: 49px;

    color: #000000;
    width: fit-content;
}
.lower p{
    color: #283050;
}
.btn_home{
    float: right;
    margin:20px 100px 20px 0px;
}
@media screen and (max-width:1000px) {
    br{
        display: none;
    }
    .padding_left{
        padding-left: 10px;
        padding-top: 20px;
    }
}
@media screen and (max-width:750px) {
.Image_Cards img{
margin: 20px 0px;
}
    .background{
        padding: 10px;
    }
    .img_home p{
        text-align: center;
    }
    .img_home h2{
        text-align: center;
    }
    .img_home{
        height: fit-content;
        background-size: cover;
    }
    .Head_line::after{
        width: 100px !important;
    }
    .btn_home{
        float: unset;
        margin:20px auto;
        display: block;
    }
.img_home p{
    font-size: 25px;
}
.img_home h2{
    font-size: 30px;
}
}